/* LESS Document */
.central-column {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
@media screen and (max-width: 1200px) {
  .central-column {
    width: 100%;
    box-sizing: border-box;
    padding-left: 2em;
    padding-right: 2em;
  }
}
.max-central {
  width: 100%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}
@media screen and (max-width: 1200px) {
  .max-central {
    width: 100%;
    box-sizing: border-box;
    padding-left: 2em;
    padding-right: 2em;
  }
}
.feature {
  box-shadow: 0 3px 3px 0px rgba(0, 0, 0, 0.2);
  text-decoration: none;
  position: relative;
  width: 100%;
  background: #278b27;
  background: #ffffff;
  color: #444444;
  border-radius: 2px;
  transition: background 0.4s ease;
}
.feature > p {
  display: none;
}
.feature br {
  display: none;
}
.feature p {
  margin: 0;
  display: inline-block;
  padding: 0;
}
.feature * {
  color: inherit;
}
@media screen and (max-width: 900px) {
  .feature {
    max-width: 100%;
  }
}
.feature.grey {
  background: #444444;
  color: #ffffff;
}
.feature.grey .cta-inline,
.feature.grey .cta {
  background: #ffffff;
  color: #444444 !important;
}
.feature.lightgrey {
  background: #999999;
  color: #ffffff;
}
.feature.lightgrey .cta-inline,
.feature.lightgrey .cta {
  background: #ffffff;
  color: #999999 !important;
}
.feature.superlightgrey {
  background: #eeeeee;
  color: #000000;
}
.feature.superlightgrey .cta-inline,
.feature.superlightgrey .cta {
  background: #000000;
  color: #eeeeee !important;
}
.feature.green {
  background: #278b27;
  color: #ffffff;
}
.feature.green .cta-inline,
.feature.green .cta {
  background: #ffffff;
  color: #278b27 !important;
}
.feature.blue {
  background: #a5b2c7;
  color: #000000;
}
.feature.blue .cta-inline,
.feature.blue .cta {
  background: #000000;
  color: #a5b2c7 !important;
}
.feature.lightgreen {
  background: #d0de33;
  color: #000000;
}
.feature.lightgreen .cta-inline,
.feature.lightgreen .cta {
  background: #000000;
  color: #d0de33 !important;
}
.feature.purple {
  background: #9a97b8;
  color: #ffffff;
}
.feature.purple .cta-inline,
.feature.purple .cta {
  background: #ffffff;
  color: #9a97b8 !important;
}
.feature.black {
  background: #212121;
  color: #ffffff;
}
.feature.black .cta-inline,
.feature.black .cta {
  background: #ffffff;
  color: #212121 !important;
}
.feature.white {
  background: #ffffff;
  color: #000000;
}
.feature.white .cta-inline,
.feature.white .cta {
  background: #000000;
  color: #ffffff !important;
}
.feature.orange {
  background: #be904d;
  color: #000000;
}
.feature.orange .cta-inline,
.feature.orange .cta {
  background: #000000;
  color: #be904d !important;
}
.feature svg .triangle {
  fill: #NaNNaNNaN;
}
.feature.grey svg .triangle {
  fill: #444444;
}
.feature.lightgrey svg .triangle {
  fill: #999999;
}
