.row {
  width: 100%;
  display: flex;
  min-height: 400px;
  justify-content: space-between;
  margin-bottom: 10px;
}
.column {
  margin: 10px;
  position: relative;
  border: 1px solid #eee;
  cursor: pointer;
  flex-grow: 1;
  background: #fff;
  flex-basis: 50%;
  padding: 10px;
}
.column.extend {
  flex-grow: 2;
  flex-basis: 100%;
}
.column.extend .col-control-extend:before {
  content: "\f344 \f340";
}
.column button[class^='col-control-'] {
  position: absolute;
  margin: 0;
  display: none;
  z-index: 50;
  font-size: 0;
  background: none;
  overflow: hidden;
  height: 35px;
  box-sizing: border-box;
  color: transparent;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}
.column button[class^='col-control-']:before {
  display: inline-block;
  -webkit-font-smoothing: antialiased;
  font: normal 30px/1 'dashicons';
  vertical-align: top;
  text-shadow: 0 0 #ffffff, 0 2px #ffffff, 2px 2px #ffffff, 2px 0 #ffffff, 0 0 #ffffff, 0 -2px #ffffff, -2px -2px #ffffff, -2px 0 #ffffff;
}
.column button[class^='col-control-']:hover {
  background: #00a0d2;
}
.column:hover button[class^='col-control-'] {
  display: block;
}
.column .col-control-add {
  top: 50%;
  right: 0;
  transform: translateX(50%) translateY(-70%);
}
.column .col-control-add:before {
  color: #5e7;
  content: "\f502";
}
.column .col-control-remove {
  top: 0;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}
.column .col-control-remove:before {
  color: #900;
  content: "\f153";
}
.column .col-control-extend {
  bottom: 0;
  left: 50%;
  transform: translateY(50%) translateX(-50%);
  min-width: 69px;
  cursor: pointer;
}
.column .col-control-extend:before {
  content: "\f340 \f344";
  color: #000;
  min-width: 60px;
}
.column .col-control-shrink {
  bottom: -28px;
  left: 43%;
  transform: translateY(-50%);
  min-width: 69px;
}
.column .col-control-shrink:before {
  content: "\f344 \f340";
  color: #000;
  min-width: 60px;
}
.column > .col-content {
  display: none;
}
.column .preview {
  float: none;
}
.column:hover .preview {
  opacity: 0.4;
}
.column button.col-control-edit {
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: 20%;
  font-weight: bold;
  box-sizing: border-box;
  padding: 0;
  line-height: 1;
  font-family: dashicons;
  text-decoration: inherit;
  font-weight: 400;
  font-style: normal;
  vertical-align: top;
  text-align: center;
  margin: auto;
  height: auto;
  width: auto;
  z-index: 30;
  transform: translate(-50%, -50%);
}
.column button.col-control-edit:after {
  content: "\f464";
  color: rgba(0, 160, 210, 0.4);
  font-size: 80px;
  border-radius: 10px;
  padding: 5px;
}
.column button.col-control-edit:hover {
  background: rgba(255, 255, 255, 0.6);
}
.column button.col-control-edit:hover:after {
  background: rgba(0, 160, 210, 0.6);
  color: #fff;
}
#wp-popup-editor-wrap-wrap {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
  background: #FFF;
  z-index: 100;
  padding: 20px;
}
